@import "variables";
@import "stars";

.card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 200px;

  border: 1px solid #f3f3f3;
  border-radius: 5px;

  padding: 15px 14px;

  &__img {
    width: 160px;
    height: 134px;
    margin: 2px 2px 29px;
    padding: 10px 0;

    border: 3px solid transparent;

    transition: transform 1s;

    &:hover {
      transform: scale(1.2) translate(0, -1px);
      border-color: #f3f3f3;
      border-radius: 4px;
    }
  }

  &__text {
    font-weight: $fw-semibold;
    font-size: 12px;
    line-height: 15px;

    margin: 0;
    margin-bottom: 5px;
  }

  &__code {
    font-weight: $fw-normal;
    font-size: 11px;
    line-height: 13px;
  }

  &__rate {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 15px;
    margin-bottom: 20px;
  }

  &__stars {
    display: flex;
  }

  &__comments {
    font-weight: $fw-normal;
    font-size: 11px;
    line-height: 13px;
    text-align: right;
  }

  &__amount {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__cost {
    font-weight: $fw-normal;
    font-size: 11px;
    line-height: 13px;
  }

  &__price {
    font-weight: $fw-bold;
    font-size: 16px;
    line-height: 19px;
  }

  &__button {
    display: flex;
    box-sizing: border-box;
    width: 170px;
    height: 40px;
    border-radius: 5px;

    margin-top: 20px;

    background-color: $color-blue;

    font-size: 14px;
    font-weight: $fw-bold;
    line-height: 40px;
    text-align: center;
    justify-content: center;
    color: white;
    text-decoration: none;
    text-transform: uppercase;

    transition: background-color 1s;

    &:hover {
      background-color: white;
      color: $color-blue;
      border: 1px solid $color-blue;
    }
  }
}
